<template>
  <article class="container mx-auto position-relative" v-if="!isLoading">
    <Alert
      :alertArrayProp="alertArray"
      v-on:on-delete-alert="deleteAlert"
      class="alert_section position-absolute"
    />
    <section class="w-80 mx-auto flex-center flex-column mb-6">
      <h1 class="d-block mb-3">修改密碼</h1>
      <div class="input-group input-group-lg mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="inputGroup-sizing-lg">新密碼</span>
        </div>
        <input
          type="text"
          class="form-control"
          aria-label="Large"
          aria-describedby="inputGroup-sizing-sm"
          v-model="newPassword"
        >
      </div>
      <div class="input-group input-group-lg mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="inputGroup-sizing-lg">確認新密碼</span>
        </div>
        <input
          type="text"
          class="form-control"
          aria-label="Large"
          aria-describedby="inputGroup-sizing-sm"
          v-model="confirmNewPassword"
        >
      </div>
      <div v-if="isAlertShow" class="text-danger mb-3">兩者密碼不同喔'</div>
      <button class="d-block btn btn-primary" @click.prevent="modifyPassword">確認修改</button>
    </section>
    <section class="w-80 mx-auto pt-6">
      <h1 class="d-block text-center">訂單列表</h1>
      <ul v-if="orderData.length !== 0 ">
        <li v-for="(order,index) in orderData" :key="index" class="border my-3 p-3">
          <h3>購買商品</h3>
          <div
            v-for="(product,index) in order.cartData"
            :key="index"
            class="d-flex align-items-center my-3"
          >
            <img :src="product.imgUrl" class="product_img">
            <pre class="ml-3 h4">{{product.fullId}}   {{product.title}}-{{product.colorObject.colorName}}-{{product.sizeObject.sizeName}}   x{{product.qty}}件   總價 {{product.totalPrice}}</pre>
          </div>
          <h3 class="mb-3">收貨資訊</h3>
          <h4 class="ml-3">
            收貨人{{order.receiverData.name}}
            連絡電話{{order.receiverData.phone}}
            收貨地址{{order.receiverData.address}}
          </h4>
        </li>
      </ul>
      <div v-else class="text-center">
        <h2>你還沒下訂任何商品喔</h2>
      </div>
    </section>
  </article>
</template>
<script>
import Alert from "./components/Alert.vue";
import AxiosClass from "@/config/axiosClass.js";
export default {
  components: {
    Alert
  },
  name: "user",
  data() {
    return {
      alertArray: [],
      orderData: [],
      newPassword: "",
      confirmNewPassword: "",
      isAlertShow: false
    };
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
    isPasswordSame() {
      if (this.newPassword === this.confirmNewPassword) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    deleteAlert(index) {
      this.alertArray.splice(index, 1);
    },
    alert(text, isDanger = false) {
      this.alertArray.push({ text: text, isDanger: isDanger });
      setTimeout(() => {
        this.alertArray.splice(0, 1);
      }, 1000);
    },
    ensureLogin() {
      if (!this.$store.state.user_email) {
        this.$router.push("/login");
      }
    },
    getOrder() {
      const getOrder = new AxiosClass("user/order", "get", null, true);
      getOrder.doAxios(
        this,
        res => {
          this.orderData = res.data.data;
          this.alert("抓取訂單成功");
        },
        () => {
          this.alert("抓取訂單失敗", true);
        }
      );
    },
    modifyPassword() {
      if (!this.isPasswordSame) {
        this.isAlertShow = true;
        return;
      } else {
        const modifyPassword = new AxiosClass(
          "user/modify_password",
          "put",
          { newPassword: this.newPassword, user_email: this.$store.state.user_email },
          true
        );
        modifyPassword.doAxios(
          this,
          () => {
            this.alert("修改密碼成功");
          },
          res => {
            console.log(res);
            this.alert("修改密碼失敗", true);
          }
        );
      }
    }
  },
  created() {
    this.ensureLogin();
    this.getOrder();
  },
  beforeUpdate() {
    this.ensureLogin();
  }
};
</script>
<style lang="scss" scoped>
.product_img {
  height: 50px;
}
.alert_section {
  right: 0;
  z-index: 10000;
}
</style>
